import React from "react";
import { animateScroll as scroll } from "react-scroll";
import {
  SidebarContainer,
  Icon,
  CloseIcon,
  SidebarWrapper,
  SidebarMenu,
  SidebarLink,
  SideContactBtnWrap,
  SidebarRoute,
} from "./SideBarElements";

const Sidebar = ({ isOpen, toggle }) => {
  const toggleFooter = () => {
    scroll.scrollToBottom();
  };

  return (
    <SidebarContainer isOpen={isOpen} onClick={toggle}>
      <Icon onClick={toggle}>
        <CloseIcon />
      </Icon>
      <SidebarWrapper>
        <SidebarMenu>
          <SidebarLink
            to="about"
            onClick={toggle}
            smooth={true}
            duration={400}
            spy={true}
            exact="true"
            offset={-100}
          >
            About
          </SidebarLink>
          <SidebarLink
            to="experience"
            onClick={toggle}
            smooth={true}
            duration={400}
            spy={true}
            exact="true"
            offset={-100}
          >
            Experience
          </SidebarLink>
          <SidebarLink
            to="portfolio"
            onClick={toggle}
            smooth={true}
            duration={400}
            spy={true}
            exact="true"
            offset={-100}
          >
            Skill
          </SidebarLink>
        </SidebarMenu>
        <SideContactBtnWrap>
          <SidebarRoute onClick={toggleFooter}>Contact</SidebarRoute>
        </SideContactBtnWrap>
      </SidebarWrapper>
    </SidebarContainer>
  );
};

export default Sidebar;
